'use client'

import { useRef, useState, useEffect } from 'react';
import { Play, Pause } from 'lucide-react';
import Image from 'next/image';

export function HeroSection() {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [videoAvailable, setVideoAvailable] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  // Verifica se √© mobile
  useEffect(() => {
    const checkIfMobile = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    checkIfMobile();
    window.addEventListener('resize', checkIfMobile);
    
    return () => window.removeEventListener('resize', checkIfMobile);
  }, []);

  // Verifica se o v√≠deo existe
  useEffect(() => {
    const checkVideoExists = async () => {
      try {
        const response = await fetch('/controle-ponto-hero.mp4', { method: 'HEAD' });
        setVideoAvailable(response.ok);
      } catch {
        setVideoAvailable(false);
      }
    };

    checkVideoExists();
  }, []);

  // Configura listeners do v√≠deo
  useEffect(() => {
    const video = videoRef.current;
    if (video && videoAvailable) {
      const handleError = () => setVideoAvailable(false);
      video.addEventListener('error', handleError);
      return () => video.removeEventListener('error', handleError);
    }
  }, [videoAvailable]);

  const togglePlay = () => {
    if (videoRef.current) {
      if (videoRef.current.paused) {
        videoRef.current.play();
        setIsPlaying(true);
      } else {
        videoRef.current.pause();
        setIsPlaying(false);
      }
    }
  };

  return (
    <section className={`relative ${isMobile ? 'h-[70vh]' : 'h-[90vh]'} min-h-[500px] flex items-center justify-center overflow-hidden`}>
      {/* V√≠deo ou Imagem de fundo */}
      <div className="absolute inset-0 z-0">
        {videoAvailable ? (
          <video
            ref={videoRef}
            autoPlay
            muted
            loop
            playsInline
            className="w-full h-full object-cover"
            poster="/video-poster.jpg"
          >
            <source src="/controle-ponto-hero.mp4" type="video/mp4" />
            <source src="/controle-ponto-hero.webm" type="video/webm" />
          </video>
        ) : (
          <Image
            src="/controle-ponto-hero.webp"
            alt="Controle de ponto inteligente"
            fill
            className="object-cover"
            priority
            quality={isMobile ? 75 : 90}
          />
        )}
        {/* Overlay escuro */}
        <div className="absolute inset-0 bg-black/50" />
      </div>

      {/* Conte√∫do */}
      <div className="relative z-10 text-center px-4 w-full">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <h1 className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold text-white mb-4 sm:mb-6 leading-tight drop-shadow-2xl">
            Controle de Ponto <span className="text-[#22b917]">Inteligente</span>
          </h1>
          
          <p className="text-lg sm:text-xl md:text-2xl text-gray-200 mb-6 sm:mb-8 max-w-2xl mx-auto">
            Tecnologia de ponta com reconhecimento facial 3D e geolocaliza√ß√£o precisa
          </p>

          {videoAvailable && (
            <button
              onClick={togglePlay}
              className="mb-8 sm:mb-12 inline-flex items-center gap-2 text-white hover:text-[#177f0f] transition-colors text-sm sm:text-base"
            >
              {isPlaying ? (
                <>
                  <Pause className="w-5 h-5 sm:w-6 sm:h-6" />
                  Pausar v√≠deo
                </>
              ) : (
                <>
                  <Play className="w-5 h-5 sm:w-6 sm:h-6" />
                  Assistir v√≠deo
                </>
              )}
            </button>
          )}

          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 sm:gap-6 md:gap-8 mb-8 sm:mb-16 px-4">
            {[
              { icon: '‚ö°', text: "Suporte R√°pido" },
              { icon: 'üõ°Ô∏è', text: "Certificado INMETRO" },
              { icon: 'üìà', text: "Redu√ß√£o de Custos" }
            ].map((item, index) => (
              <div 
                key={index}
                className="bg-white/10 backdrop-blur-sm p-4 sm:p-6 rounded-xl border border-white/20 hover:bg-white/20 transition-all"
              >
                <div className="text-2xl sm:text-3xl mb-2 sm:mb-4">{item.icon}</div>
                <p className="font-semibold text-white text-sm sm:text-base">{item.text}</p>
              </div>
            ))}
          </div>

          <button 
            className="bg-[#177f0f] text-white px-6 py-4 sm:px-8 sm:py-6 text-base sm:text-lg hover:bg-[#125f0b] shadow-xl transition-all rounded-lg"
            onClick={() => window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' })}
          >
            Solicitar Demonstra√ß√£o
          </button>
        </div>
      </div>
    </section>
  );
}